section.home {
  @include position($top: 50%, $left: 50%);
  @include translate($x: -50%, $y: -100%);

  h1 {
    font-size: 70px;
    font-family: $font-bold;
  }

  p {
    margin-top: 40px;
    margin-bottom: 40px;
    font-size: 20px;
    line-height: 28px;
    font-style: italic;
    font-family: $font-light;
  }

  .common-text {
    color: $color-base;
    font-family: $font-bold;
  }

  .css-9y1egq-MuiButtonBase-root-MuiButton-root {
    color: $color-base;
    border-color: $color-base;
    transition: none;
    
    &:hover {
      background-color: unset;
    }
  }
}
